<template>
 <div id="edit-employee">
  <h3>Edit Employee</h3>
  <div class="row">
   <form @submit.prevent="updateEmployee" class="col s12">
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.id" required disabled>
      <!-- <label>Employee ID</label> -->
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.name" required>
      <!-- <label>name</label> -->
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.email" required>
      <!-- <label>Email</label> -->
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.department" required>
      <!-- <label>Department</label> -->
     </div>
    </div>
    <div class="row">
     <div class="input-field col s12">
      <input type="text" v-model="employee.position" required>
      <!-- <label>Position</label> -->
     </div>
    </div>
    <button class="btn" type="submit">Update</button>
    <router-link class="btn grey" to="/">Cancel</router-link>
   </form>
  </div> 
 </div>
</template>
<script>
import db from '../firebase_services/firebaseinit'
export default {
 name:'edit-employee',
 data(){
  return{
   employee:{
    id:'',
    email:'',
    position:'',
    name:'',
    department:''
   }
  }
 },
 
 beforeRouteEnter (to,from,next) {
  db.collection('employees').where('id','==',to.params.employee_id).get()
  .then(querySnapshot=>{
   querySnapshot.forEach(doc=>{
    next(vm=>{
     vm.employee=doc.data()
    })
   })
  })
 },
 watch:{
  '$route':'fetchData'
 },
 methods:{
  fetchData(){
   db.collection('employees').where('id','==', this.$route.params.employee_id).get()
   .then(querySnapshot=>{
    querySnapshot.forEach(doc=>{
     this.employee = doc.data()
    })
   })
  },
  updateEmployee(){
   
   db.collection('employees').where('id','==', this.$route.params.employee_id).get()
   .then(querySnapshot=>{
    querySnapshot.forEach(doc=>{
     doc.ref.update(this.employee)
     .then(()=>{
      this.$router.push({name:'view-employee', params:{employee_id:this.employee.id}},()=>{})
      })
    })
   })
   
  }
 }
}
</script>